import {
	getDataTingkat,
	postDataTingkat,
	putDataTingkat,
	deleteDataTingkat
} from '@/config/tingkat'

var dataTingkat = {
	namespaced: true,
	
	state: {
		dataTingkat: [],
		status: null,
		isLoading: false
	},

	mutations: {
		updateDataTingkat (state, payload) {
			state.dataTingkat = payload
		},
		updateStatus (state, payload) {
			state.status = payload
		},
		updateIsLoading (state, payload) {
			state.isLoading = payload
		}
	},

	actions: {
		updateIsLoading (context, payload) {
			context.commit('updateIsLoading', payload)
		},
		getDataTingkat (context, payload) {
			getDataTingkat()
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataTingkat', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		postDataTingkat (context, payload) {
			postDataTingkat(payload)
				.then(res => {
					if (res.status === 201) {
						context.dispatch('getDataTingkat')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		putDataTingkat (context, payload) {
			putDataTingkat(payload.id, payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataTingkat')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		deleteDataTingkat (context, payload) {
			deleteDataTingkat(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataTingkat')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		}
	}
}

export default dataTingkat
