import {
	getDataJurusan,
	postDataJurusan,
	putDataJurusan,
	deleteDataJurusan
} from '@/config/jurusan'

var dataJurusan = {
	namespaced: true,
	
	state: {
		dataJurusan: [],
		status: null,
		isLoading: false
	},

	mutations: {
		updateDataJurusan (state, payload) {
			state.dataJurusan = payload
		},
		updateStatus (state, payload) {
			state.status = payload
		},
		updateIsLoading (state, payload) {
			state.isLoading = payload
		}
	},

	actions: {
		updateIsLoading (context, payload) {
			context.commit('updateIsLoading', payload)
		},
		getDataJurusan (context, payload) {
			getDataJurusan()
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataJurusan', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		postDataJurusan (context, payload) {
			postDataJurusan(payload)
				.then(res => {
					if (res.status === 201) {
						context.dispatch('getDataJurusan')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		putDataJurusan (context, payload) {
			putDataJurusan(payload.id, payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataJurusan')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		deleteDataJurusan (context, payload) {
			deleteDataJurusan(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataJurusan')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		}
	}
}

export default dataJurusan
