import http from './http'

export function getDataJenjang() {
	return http.get('master/jenjang')
}

export function postDataJenjang(data) {
	return http.post('master/jenjang', data)
}

export function putDataJenjang(id, data) {
	return http.put('master/jenjang/'+id, data)
}

export function deleteDataJenjang(id) {
	return http.delete('master/jenjang/'+id)
}
