import http from './http'

export function getDataPelajaran() {
	return http.get('master/pelajaran')
}

export function postDataPelajaran(data) {
	return http.post('master/pelajaran', data)
}

export function putDataPelajaran(id, data) {
	return http.put('master/pelajaran/'+id, data)
}

export function deleteDataPelajaran(id) {
	return http.delete('master/pelajaran/'+id)
}
