import http from './http'

export function getDataKurikulum() {
	return http.get('master/kurikulum')
}

export function postDataKurikulum(data) {
	return http.post('master/kurikulum', data)
}

export function putDataKurikulum(id, data) {
	return http.put('master/kurikulum/'+id, data)
}

export function deleteDataKurikulum(id) {
	return http.delete('master/kurikulum/'+id)
}
