import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: Home,
    // meta: { requiresAuth: true },
    children: [
      {
        path: '/',
        name: 'dashboard',
        component: () => import('../views/Dashboard.vue')
      },
      {
        path: '/peserta',
        name: 'peserta',
        component: () => import('../views/Peserta.vue')
      },
      {
        path: '/unit',
        name: 'unit',
        component: () => import('../views/Unit.vue')
      },
      {
        path: '/berita',
        name: 'berita',
        component: () => import('../views/Berita.vue')
      },
      {
        path: '/tambah-berita',
        name: 'tambah-berita',
        component: () => import('../components/Berita/TambahBerita.vue')
      },
      {
        path: '/edit-berita/:id',
        name: 'edit-berita',
        component: () => import('../components/Berita/EditBerita.vue'),
        props: true
      },
      {
        path: '/upload',
        name: 'upload',
        component: () => import('../views/Upload.vue')
      },
      {
        path: '/sekolah',
        name: 'sekolah',
        component: () => import('../views/Sekolah.vue')
      },
      {
        path: '/ptn',
        name: 'ptn',
        component: () => import('../views/PTN.vue')
      },
      {
        path: '/akreditasi',
        name: 'akreditasi',
        component: () => import('../views/Akreditasi.vue')
      },
      {
        path: '/prestasi',
        component: () => import('../views/Prestasi.vue'),
        children: [
          {
            path: '/prestasi',
            name: 'prestasi',
            component: () => import('../components/Prestasi/Tingkat.vue')
          },
          {
            path: '/bidang',
            name: 'bidang',
            component: () => import('../components/Prestasi/Bidang.vue')
          }
        ]
      },
      {
        path: '/jenjang',
        name: 'jenjang',
        component: () => import('../views/Jenjang.vue')
      },
      {
        path: '/kelas',
        name: 'kelas',
        component: () => import('../views/Kelas.vue')
      },
      {
        path: '/jurusan',
        name: 'jurusan',
        component: () => import('../views/Jurusan.vue')
      },
      {
        path: '/kurikulum',
        name: 'kurikulum',
        component: () => import('../views/Kurikulum.vue')
      },
      {
        path: '/pelajaran',
        name: 'pelajaran',
        component: () => import('../views/Pelajaran.vue')
      },
    ]
  },
  {
    path: '/login',
    name: 'login',
    component: () => import('../views/Login.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  const requiresAuth = to.matched.some(route => route.meta.requiresAuth)
  const isLoggedIn = window.localStorage.getItem('token_admin')

  if (requiresAuth && !isLoggedIn){ 
    next({ name: 'login' })
  }else if(isLoggedIn && to.name === 'login') {
    next({ name: 'dashboard' })
  }else if (requiresAuth && isLoggedIn) {
    next()
  }else {
    next()
  }
})

export default router
