import {
	getDataKategori,
	postDataKategori,
	putDataKategori,
	deleteDataKategori
} from '@/config/kategori'

var dataKategori = {
	namespaced: true,
	
	state: {
		dataKategori: [],
		status: null,
		isLoading: false
	},

	mutations: {
		updateDataKategori (state, payload) {
			state.dataKategori = payload
		},
		updateStatus (state, payload) {
			state.status = payload
		},
		updateIsLoading (state, payload) {
			state.isLoading = payload
		}
	},

	actions: {
		updateIsLoading (context, payload) {
			context.commit('updateIsLoading', payload)
		},
		getDataKategori (context, payload) {
			getDataKategori()
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataKategori', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		postDataKategori (context, payload) {
			postDataKategori(payload)
				.then(res => {
					if (res.status === 201) {
						context.dispatch('getDataKategori')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		putDataKategori (context, payload) {
			putDataKategori(payload.id, payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataKategori')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		deleteDataKategori (context, payload) {
			deleteDataKategori(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataKategori')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		}
	}
}

export default dataKategori
