import http from './http'

export function getDataKategori() {
	return http.get('kategori')
}

export function postDataKategori(data) {
	return http.post('kategori', data)
}

export function putDataKategori(data, id) {
	return http.put('kategori/'+id, data)
}

export function deleteDataKategori(id) {
	return http.delete('kategori/'+id)
}
