import {
	getDataKurikulum,
	postDataKurikulum,
	putDataKurikulum,
	deleteDataKurikulum
} from '@/config/kurikulum'

var dataKurikulum = {
	namespaced: true,
	
	state: {
		dataKurikulum: [],
		status: null,
		isLoading: false
	},

	mutations: {
		updateDataKurikulum (state, payload) {
			state.dataKurikulum = payload
		},
		updateStatus (state, payload) {
			state.status = payload
		},
		updateIsLoading (state, payload) {
			state.isLoading = payload
		}
	},

	actions: {
		updateIsLoading (context, payload) {
			context.commit('updateIsLoading', payload)
		},
		getDataKurikulum (context, payload) {
			getDataKurikulum()
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataKurikulum', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		postDataKurikulum (context, payload) {
			postDataKurikulum(payload)
				.then(res => {
					if (res.status === 201) {
						context.dispatch('getDataKurikulum')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		putDataKurikulum (context, payload) {
			putDataKurikulum(payload.id, payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataKurikulum')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		deleteDataKurikulum (context, payload) {
			deleteDataKurikulum(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataKurikulum')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		}
	}
}

export default dataKurikulum
