import {
	getDataPelajaran,
	postDataPelajaran,
	putDataPelajaran,
	deleteDataPelajaran
} from '@/config/pelajaran'

var dataPelajaran = {
	namespaced: true,
	
	state: {
		dataPelajaran: [],
		status: null,
		isLoading: false
	},

	mutations: {
		updateDataPelajaran (state, payload) {
			state.dataPelajaran = payload
		},
		updateStatus (state, payload) {
			state.status = payload
		},
		updateIsLoading (state, payload) {
			state.isLoading = payload
		}
	},

	actions: {
		updateIsLoading (context, payload) {
			context.commit('updateIsLoading', payload)
		},
		getDataPelajaran (context, payload) {
			getDataPelajaran()
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataPelajaran', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		postDataPelajaran (context, payload) {
			postDataPelajaran(payload)
				.then(res => {
					if (res.status === 201) {
						context.dispatch('getDataPelajaran')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		putDataPelajaran (context, payload) {
			putDataPelajaran(payload.id, payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataPelajaran')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		deleteDataPelajaran (context, payload) {
			deleteDataPelajaran(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataPelajaran')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		}
	}
}

export default dataPelajaran
