import http from './http'

export function postDataPtn(data) {
	return http.post('ptn/upload', data)
}

export function getDataPtn(data) {
	return http.get('ptn?page='+data+'&extended=1')
}

export function deleteDataPtn(id) {
	return http.delete('ptn/'+id)
}

export function putDataPtn(id, data) {
	return http.put('ptn/'+id, data)
}