import http from './http'

export function getDataTingkat() {
	return http.get('master/tingkat')
}

export function postDataTingkat(data) {
	return http.post('master/tingkat', data)
}

export function putDataTingkat(id, data) {
	return http.put('master/tingkat/'+id, data)
}

export function deleteDataTingkat(id) {
	return http.delete('master/tingkat/'+id)
}
