import http from './http'

export function getDataUnit() {
	return http.get('unit')
}

export function postDataUnit(data) {
	return http.post('unit', data)
}

export function putDataUnit(id, data) {
	return http.put('unit/'+id, data)
}

export function deleteDataUnit(id) {
	return http.delete('unit/'+id)
}
