import Vue from 'vue'
import Vuex from 'vuex'

import dataAkreditasi from './akreditasi'
import dataBidang from './bidang'
import dataJenjang from './jenjang'
import dataJurusan from './jurusan'
import dataKelas from './kelas'
import dataTingkat from './tingkat'
import dataKurikulum from './kurikulum'
import dataPelajaran from './pelajaran'
import dataUnit from './unit'
import dataPeserta from './peserta'
import dataTempat from './tempat'
import dataPtn from './ptn'
import dataSekolah from './sekolah'
import dataBerita from './berita'
import dataKategori from './kategori'
import dataFileupload from './fileupload'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    modalToken: false
  },
  mutations: {
    updateModalToken (state, payload) {
      state.modalToken = payload
    }
  },
  actions: {
    updateModalToken (context, payload) {
      context.commit('updateModalToken', payload)
    }
  },
  modules: {
    dataFileupload: dataFileupload,
    dataKategori: dataKategori,
    dataBerita: dataBerita,
    dataSekolah: dataSekolah,
    dataPtn: dataPtn,
    dataTempat: dataTempat,
    dataPeserta: dataPeserta,
    dataUnit: dataUnit,
    dataPelajaran: dataPelajaran,
    dataKurikulum: dataKurikulum,
    dataTingkat: dataTingkat,
    dataKelas: dataKelas,
    dataJurusan: dataJurusan,
    dataJenjang: dataJenjang,
    dataBidang: dataBidang,
    dataAkreditasi: dataAkreditasi
  }
})
