import http from './http'

export function postDataSekolah(data) {
	return http.post('sekolah/upload', data)
}

export function getDataSekolah() {
	return http.get('sekolah')
}

export function deleteDataSekolah(id) {
	return http.delete('sekolah/'+id)
}

export function putDataSekolah(id, data) {
	return http.put('sekolah/'+id, data)
}

export function getDataSekolahFilter(data) {
	return http.get(`sekolah?provinsi_id=${data.provinsi_id}&kabupaten_id=${data.kabupaten_id}`)
}