import {
	getDataFileupload,
	postDataFileupload,
	// putDataFileupload,
	deleteDataFileupload
} from '@/config/fileupload'

var dataFileupload = {
	namespaced: true,
	
	state: {
		dataFileupload: [],
		status: null,
		isLoading: false
	},

	mutations: {
		updateDataFileupload (state, payload) {
			state.dataFileupload = payload
		},
		updateStatus (state, payload) {
			state.status = payload
		},
		updateIsLoading (state, payload) {
			state.isLoading = payload
		}
	},

	actions: {
		updateIsLoading (context, payload) {
			context.commit('updateIsLoading', payload)
		},
		getDataFileupload (context, payload) {
			getDataFileupload()
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataFileupload', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		postDataFileupload (context, payload) {
			postDataFileupload(payload)
				.then(res => {
					if (res.status === 201) {
						context.dispatch('getDataFileupload')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		// putDataFileupload (context, payload) {
		// 	putDataFileupload(payload.id, payload)
		// 		.then(res => {
		// 			if (res.status === 200) {
		// 				context.dispatch('getDataFileupload')
		// 				context.commit('updateStatus', true)
		// 				context.dispatch('updateIsLoading', false)
		// 			}
		// 		})
		// 		.catch(err => {
		// 			context.commit('updateStatus', false)
		// 			context.dispatch('updateIsLoading', false)
		// 		})
		// },
		deleteDataFileupload (context, payload) {
			deleteDataFileupload(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataFileupload')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		}
	}
}

export default dataFileupload
