import {
	deleteDataPtn,
	postDataPtn,
	putDataPtn,
	getDataPtn
} from '@/config/ptn'

var dataPtn = {
	namespaced: true,
	
	state: {
		dataPtn: [],
		status: null,
		isLoading: false,
		lengthPage: 0
	},

	mutations: {
		updateDataPtn (state, payload) {
			state.dataPtn = payload
		},
		updateLengthPage (state, payload) {
			state.lengthPage = payload
		},
		updateStatus (state, payload) {
			state.status = payload
		},
		updateIsLoading (state, payload) {
			state.isLoading = payload
		}
	},

	actions: {
		updateIsLoading (context, payload) {
			context.commit('updateIsLoading', payload)
		},
		getDataPtn (context, payload) {
			getDataPtn(payload)
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataPtn', res.data.data)
						context.commit('updateLengthPage', res.data.meta.last_page)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		postDataPtn (context, payload) {
			postDataPtn(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataPtn', 1)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		putDataPtn (context, payload) {
			putDataPtn(payload.id, payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataPtn', 1)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		deleteDataPtn (context, payload) {
			deleteDataPtn(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataPtn', 1)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
	}
}

export default dataPtn
