import http from './http'

export function getDataKelas() {
	return http.get('master/kelas')
}

export function postDataKelas(data) {
	return http.post('master/kelas', data)
}

export function putDataKelas(id, data) {
	return http.put('master/kelas/'+id, data)
}

export function deleteDataKelas(id) {
	return http.delete('master/kelas/'+id)
}
