import {
	getDataKelas,
	postDataKelas,
	putDataKelas,
	deleteDataKelas
} from '@/config/kelas'

var dataKelas = {
	namespaced: true,
	
	state: {
		dataKelas: [],
		status: null,
		isLoading: false
	},

	mutations: {
		updateDataKelas (state, payload) {
			state.dataKelas = payload
		},
		updateStatus (state, payload) {
			state.status = payload
		},
		updateIsLoading (state, payload) {
			state.isLoading = payload
		}
	},

	actions: {
		updateIsLoading (context, payload) {
			context.commit('updateIsLoading', payload)
		},
		getDataKelas (context, payload) {
			getDataKelas()
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataKelas', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		postDataKelas (context, payload) {
			postDataKelas(payload)
				.then(res => {
					if (res.status === 201) {
						context.dispatch('getDataKelas')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		putDataKelas (context, payload) {
			putDataKelas(payload.id, payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataKelas')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		deleteDataKelas (context, payload) {
			deleteDataKelas(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataKelas')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		}
	}
}

export default dataKelas
