import {
	getDataBidangPrestasi,
	postDataBidangPrestasi,
	putDataBidangPrestasi,
	deleteDataBidangPrestasi
} from '@/config/bidangPrestasi'

var dataBidangPrestasi = {
	namespaced: true,
	
	state: {
		dataBidang: [],
		status: null,
		isLoading: false
	},

	mutations: {
		updateDataBidangPrestasi (state, payload) {
			state.dataBidang = payload
		},
		updateStatus (state, payload) {
			state.status = payload
		},
		updateIsLoading (state, payload) {
			state.isLoading = payload
		}
	},

	actions: {
		updateIsLoading (context, payload) {
			context.commit('updateIsLoading', payload)
		},
		getDataBidang (context, payload) {
			getDataBidangPrestasi()
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataBidangPrestasi', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		postDataBidang (context, payload) {
			postDataBidangPrestasi(payload)
				.then(res => {
					if (res.status === 201) {
						context.dispatch('getDataBidang')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		putDataBidang (context, payload) {
			putDataBidangPrestasi(payload.id, payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataBidang')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		deleteDataBidang (context, payload) {
			deleteDataBidangPrestasi(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataBidang')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		}
	}
}

export default dataBidangPrestasi
