import http from './http'

export function getDataBerita() {
	return http.get('newsletter')
}

export function getDetailBerita(id) {
	return http.get('newsletter/'+id)
}

export function postDataBerita(data) {
	return http.post('newsletter', data)
}

export function putDataBerita(id, data) {
	return http.put('newsletter/'+id, data)
}

export function deleteDataBerita(id) {
	return http.delete('newsletter/'+id)
}
