import {
	getDataJenjang,
	postDataJenjang,
	putDataJenjang,
	deleteDataJenjang
} from '@/config/jenjang'

var dataJenjang = {
	namespaced: true,
	
	state: {
		dataJenjang: [],
		status: null,
		isLoading: false
	},

	mutations: {
		updateDataJenjang (state, payload) {
			state.dataJenjang = payload
		},
		updateStatus (state, payload) {
			state.status = payload
		},
		updateIsLoading (state, payload) {
			state.isLoading = payload
		}
	},

	actions: {
		updateIsLoading (context, payload) {
			context.commit('updateIsLoading', payload)
		},
		getDataJenjang (context, payload) {
			getDataJenjang()
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataJenjang', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		postDataJenjang (context, payload) {
			postDataJenjang(payload)
				.then(res => {
					if (res.status === 201) {
						context.dispatch('getDataJenjang')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		putDataJenjang (context, payload) {
			putDataJenjang(payload.id, payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataJenjang')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		deleteDataJenjang (context, payload) {
			deleteDataJenjang(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataJenjang')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		}
	}
}

export default dataJenjang
