import {
	getDataPeserta,
	postDataPeserta,
	// putDataPeserta,
	deleteDataPeserta
} from '@/config/peserta'

var dataPeserta = {
	namespaced: true,

	state: {
		dataPeserta: [],
		status: null,
		isLoading: false
	},

	mutations: {
		updateDataPeserta (state, payload) {
			state.dataPeserta = payload
		},
		updateStatus (state, payload) {
			state.status = payload
		},
		updateIsLoading (state, payload) {
			state.isLoading = payload
		}
	},

	actions: {
		updateIsLoading (context, payload) {
			context.commit('updateIsLoading', payload)
		},
		getDataPeserta (context, payload) {
			getDataPeserta()
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataPeserta', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		postDataPeserta (context, payload) {
			postDataPeserta(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataPeserta')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		// putDataPeserta (context, payload) {
		// 	putDataPeserta(payload.id, payload)
		// 		.then(res => {
		// 			if (res.status === 200) {
		// 				context.dispatch('getDataPeserta')
		// 				context.commit('updateStatus', true)
		// 				context.dispatch('updateIsLoading', false)
		// 			}
		// 		})
		// 		.catch(err => {
		// 			context.commit('updateStatus', false)
		// 			context.dispatch('updateIsLoading', false)
		// 		})
		// }
		deleteDataPeserta (context, payload) {
			deleteDataPeserta(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataPeserta')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		}
	}
}

export default dataPeserta
