import {
	getDataSekolahFilter,
	getDataSekolah,
	deleteDataSekolah,
	putDataSekolah,
	postDataSekolah
} from '@/config/sekolah'

var dataSekolah = {
	namespaced: true,
	
	state: {
		dataSekolah: [],
		status: null,
		isLoading: false
	},

	mutations: {
		updateDataSekolah (state, payload) {
			state.dataSekolah = payload
		},
		updateStatus (state, payload) {
			state.status = payload
		},
		updateIsLoading (state, payload) {
			state.isLoading = payload
		}
	},

	actions: {
		updateIsLoading (context, payload) {
			context.commit('updateIsLoading', payload)
		},
		getDataSekolahFilter (context, payload) {
			getDataSekolahFilter(payload)
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataSekolah', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		getDataSekolah (context, payload) {
			getDataSekolah(payload)
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataSekolah', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		postDataSekolah (context, payload) {
			postDataSekolah(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataSekolah')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		putDataSekolah (context, payload) {
			putDataSekolah(payload.kode_sekolah, payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataSekolah')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		deleteDataSekolah (context, payload) {
			deleteDataSekolah(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataSekolah')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
	}
}

export default dataSekolah
