import {
	getDataBerita,
	getDetailBerita,
	postDataBerita,
	putDataBerita,
	deleteDataBerita
} from '@/config/berita'

var dataBerita = {
	namespaced: true,
	
	state: {
		dataBerita: [],
		status: null,
		isLoading: false
	},

	mutations: {
		updateDataBerita (state, payload) {
			state.dataBerita = payload
		},
		updateStatus (state, payload) {
			state.status = payload
		},
		updateIsLoading (state, payload) {
			state.isLoading = payload
		}
	},

	actions: {
		updateIsLoading (context, payload) {
			context.commit('updateIsLoading', payload)
		},
		getDataBerita (context, payload) {
			getDataBerita()
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataBerita', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		getDetailBerita (context, payload) {
			getDetailBerita(payload)
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataBerita', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		postDataBerita (context, payload) {
			postDataBerita(payload)
				.then(res => {
					if (res.status === 201) {
						context.dispatch('getDataBerita')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		putDataBerita (context, payload) {
			putDataBerita(payload.id, payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataBerita')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		deleteDataBerita (context, payload) {
			deleteDataBerita(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataBerita')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		}
	}
}

export default dataBerita
