// src/plugins/vuetify.js

import Vue from 'vue'
import Vuetify, {
	VApp, VNavigationDrawer,
	VAppBar, VContent,
	VContainer, VFooter,
	VList, VListItem, VListItemIcon, 
	VListItemContent, VListItemTitle, VListItemGroup,
	VListItemAvatar, VListItemAction,
	VAvatar, VBtn, VIcon, VListGroup,
	VAppBarNavIcon, VDivider, VAutocomplete,
	VRow, VCol, VCard, VCardTitle, VCardText,
	VSpacer, VCardActions, VForm,
	VSelect, VDataTable, VAlert, VTextField, VFileInput,
	VChip, VOverlay, VSkeletonLoader, VPagination
} from 'vuetify/lib'
import { Ripple } from 'vuetify/lib/directives'
// import 'vuetify/dist/vuetify.min.css'

Vue.use(Vuetify, {
	components: {
		VApp, VNavigationDrawer,
		VAppBar, VContent,
		VContainer, VFooter,
		VList, VListItem, VListItemIcon, 
		VListItemContent, VListItemTitle, VListItemGroup,
		VListItemAvatar, VListItemAction,
		VAvatar, VBtn, VIcon, VListGroup,
		VAppBarNavIcon, VDivider, VAutocomplete,
		VRow, VCol, VCard, VCardTitle, VCardText,
		VSpacer, VCardActions, VForm,
		VSelect, VDataTable, VAlert, VTextField, VFileInput,
		VChip, VOverlay, VSkeletonLoader, VPagination
	},
  directives: {
    Ripple
  }
})

const opts = {}

export default new Vuetify(opts)