import http from './http'

export function getDataAkreditasi() {
	return http.get('master/akreditasi')
}

export function postDataAkreditasi(data) {
	return http.post('master/akreditasi', data)
}

export function putDataAkreditasi(id, data) {
	return http.put('master/akreditasi/'+id, data)
}

export function deleteDataAkreditasi(id) {
	return http.delete('master/akreditasi/'+id)
}
