import http from './http'

export function getDataJurusan() {
	return http.get('master/jurusan')
}

export function postDataJurusan(data) {
	return http.post('master/jurusan', data)
}

export function putDataJurusan(id, data) {
	return http.put('master/jurusan/'+id, data)
}

export function deleteDataJurusan(id) {
	return http.delete('master/jurusan/'+id)
}
