import {
	getDataAkreditasi,
	postDataAkreditasi,
	putDataAkreditasi,
	deleteDataAkreditasi
} from '@/config/akreditasi'

var dataAkreditasi = {
	namespaced: true,
	
	state: {
		dataAkreditasi: [],
		status: null,
		isLoading: false
	},

	mutations: {
		updateDataAkreditasi (state, payload) {
			state.dataAkreditasi = payload
		},
		updateStatus (state, payload) {
			state.status = payload
		},
		updateIsLoading (state, payload) {
			state.isLoading = payload
		}
	},

	actions: {
		updateIsLoading (context, payload) {
			context.commit('updateIsLoading', payload)
		},
		getDataAkreditasi (context, payload) {
			getDataAkreditasi()
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataAkreditasi', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		postDataAkreditasi (context, payload) {
			postDataAkreditasi(payload)
				.then(res => {
					// console.log(res)
					if (res.status === 201) {
						context.dispatch('getDataAkreditasi')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		putDataAkreditasi (context, payload) {
			putDataAkreditasi(payload.id, payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataAkreditasi')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		deleteDataAkreditasi (context, payload) {
			deleteDataAkreditasi(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataAkreditasi')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		}
	}
}

export default dataAkreditasi
