import {
	getDataUnit,
	postDataUnit,
	putDataUnit,
	deleteDataUnit
} from '@/config/unit'

var dataUnit = {
	namespaced: true,
	
	state: {
		dataUnit: [],
		status: null,
		isLoading: false
	},

	mutations: {
		updateDataUnit (state, payload) {
			state.dataUnit = payload
		},
		updateStatus (state, payload) {
			state.status = payload
		},
		updateIsLoading (state, payload) {
			state.isLoading = payload
		}
	},

	actions: {
		updateIsLoading (context, payload) {
			context.commit('updateIsLoading', payload)
		},
		getDataUnit (context, payload) {
			getDataUnit()
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataUnit', res.data.data)
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		postDataUnit (context, payload) {
			postDataUnit(payload)
				.then(res => {
					if (res.status === 201) {
						context.dispatch('getDataUnit')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		putDataUnit (context, payload) {
			putDataUnit(payload.id, payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataUnit')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		},
		deleteDataUnit (context, payload) {
			deleteDataUnit(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataUnit')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(err => {
					context.commit('updateStatus', false)
					context.dispatch('updateIsLoading', false)
				})
		}
	}
}

export default dataUnit
