import http from './http'

export function getDataBidangPrestasi() {
	return http.get('master/bidangprestasi')
}

export function postDataBidangPrestasi(data) {
	return http.post('master/bidangprestasi', data)
}

export function putDataBidangPrestasi(id, data) {
	return http.put('master/bidangprestasi/'+id, data)
}

export function deleteDataBidangPrestasi(id) {
	return http.delete('master/bidangprestasi/'+id)
}
